.Accordion {
  padding: 0 var(--spacing-medium);
}

.Accordion__Item {
  border-bottom: 1px solid #f3f4f8;
  padding: 18px 0;
}

.Accordion .Item__Header {
  align-items: center;
  font-size: 18px;
  line-height: 22px;
  display: flex;
}

.Accordion .Item__Header:hover {
  cursor: pointer;
}

.Accordion .Item__Header__Icon {
  width: 20px;
  height: 18px;
  justify-content: center;
  align-items: center;
  margin-right: 19px;
  display: flex;
}

.Accordion .Item__Header__IndicateIcon {
  margin-left: auto;
}

.Accordion .Item__ContentContainer {
  max-height: 0;
  font-size: 14px;
  transition: max-height .3s ease-in-out;
  overflow: hidden;
}

.Accordion .Item__Content {
  padding-top: 15px;
}

